function InfiniteCarousel({ images, badge, direction = 'left', speed = 40, align = 'left' }) {
  const containerRef = useRef(null);
  const [offset, setOffset] = useState(0);
  const [groupWidth, setGroupWidth] = useState(0);

  // 20
  const GROUP_COUNT = 20;
  const allImages = Array(GROUP_COUNT).fill(images).flat();

  useEffect(() => {
    if (containerRef.current) {
      // 
      setGroupWidth(containerRef.current.scrollWidth / GROUP_COUNT);
    }
  }, [images]);

  // 
  useEffect(() => {
    let raf;
    let lastTimestamp = performance.now();
    function animate(now) {
      const elapsed = now - lastTimestamp;
      lastTimestamp = now;
      setOffset(prev => {
        let next = direction === 'left'
          ? prev - (elapsed * 0.12)
          : prev + (elapsed * 0.12);

        //  [ -groupWidth * GROUP_COUNT / 2, 0 ]
        const min = -groupWidth * GROUP_COUNT / 2;
        const max = 0;

        if (next < min) next = max;
        if (next > max) next = min;

        return next;
      });
      raf = requestAnimationFrame(animate);
    }
    raf = requestAnimationFrame(animate);
    return () => cancelAnimationFrame(raf);
  }, [groupWidth, direction]);

  return (
    <div
      style={{
        width: '100%',
        overflow: 'hidden',
        position: 'relative',
        left: 0,
        height: 375,
        margin: '28px 0',
        display: 'flex',
        justifyContent: 'flex-start',
      }}
    >
      <div
        ref={containerRef}
        style={{
          display: 'flex',
          flexDirection: 'row',
          gap: 44,
          transform: `scale(0.75) translateX(${offset}px)`,
          transformOrigin: align === 'right' ? 'right' : 'left',
          willChange: 'transform',
          transition: 'none',
        }}
      >
        {allImages.map((img, i) => (
          <div key={i} style={{ position: 'relative', width: 281, height: 375 }}>
            <img
              src={img}
              alt={`cover${i}`}
              style={{ width: '100%', height: '100%', objectFit: 'cover', borderRadius: 0 }}
            />
            <img
              src={badge}
              alt={`jiaobiao${i}`}
              style={{
                position: 'absolute',
                right: 0,
                top: 0,
                width: 120,
                height: 121,
                objectFit: 'cover',
                borderRadius: 0,
              }}
            />
          </div>
        ))}
      </div>
    </div>
  );
}

